function App() {
  const [paperIndex, changePaperIndex] = useState(0);
  const [popupHeight, setPopupHeight] = useState("auto");
  const [webinarIndex, changeWebinarIndex] = useState(0);

  return (
    <div className="wrapper" style={{ maxHeight: `${popupHeight}` }}>
      <Header />
      <main className="main">
        <section className="news">
          <div className="container" style={{ backgroundImage: "url('img/news-img.png')" }}>
            <section className="body">
              <h3>Запишитесь на новый вебинар</h3>
              <h2>“Блаблабла”</h2>
              <span className="time" style={{
                backgroundImage: "url('icons/timer.svg')"
              }}>Начало 23 июля, 22:00</span>
              <button>Открыть</button>
            </section>
          </div>
        </section>
        <section className="about" id="about">
          <div className="container">
            <h3 className="titleSection">О нас</h3>
            <div className="body">
              {about.map(obj => {
                return (
                  <section key={obj.title} className="item">
                    <h3 style={{ backgroundImage: `url('${obj.src}')` }}>{obj.title}</h3>
                    <span>{obj.text}</span>
                  </section>
                )
              })}
            </div>
          </div>
        </section>
        <section className="articles" id="articles">
          <h3 className="titleSection">Статьи</h3>
          <div className="container">
            <PaperSlider value={[papers.map((obj) => {
              return (<Paper onClickPaper={() => changePaperIndex(obj.key)} name={obj.name} img={obj.img} text={obj.text} time={obj.time} author={obj.author} />)
            })]} />
          </div>
        </section>
        <section className="webinars" id="webinars">
          <h3 className="titleSection">Видеокурсы</h3>
          <div className="container">
            {/* <WebinarSlider /> */}
            <WebinarSlider value={[webinars.map((obj) => {
              return (<Webinar onClickWebinar={() => changeWebinarIndex(obj.key)} name={obj.name} img={obj.img} text={obj.text} author={obj.author} iframe={obj.iframe} />)
            })]} />
          </div>
        </section>
        <section className="partners" id="partners">
          <h3 className="titleSection">Партнеры</h3>
          <div className="container">
            <PartnerSlider />
          </div>
        </section>
      </main>
      {paperIndex && <PaperPopup name={papers[paperIndex - 1].name} text={papers[paperIndex - 1].text} onClose={() => { setPopupHeight("100%"); changePaperIndex(0) }} onLoad={setPopupHeight} />}

      {webinarIndex && <WebinarPopup name={webinars[webinarIndex - 1].name} iframe={webinars[webinarIndex - 1].iframe} onClose={() => { setPopupHeight("100%"); changeWebinarIndex(0) }} onLoad={setPopupHeight} />}
    </div >
  );
}

export default App;